import React, { useState } from 'react'
import styled from 'styled-components'
import { Container } from '../globalStyle'
import Logo from '../img/logo.svg'
import  { useTranslation } from 'react-i18next'

const Nav = styled.nav`
    position: relative;
    min-height: 104px;
    background: #000;
    display: flex;
    align-items: center;
    z-index: 100;
    @media(max-width: 992px) {
        min-height: 72px;
    }
`

const NavWrapper = styled.div`
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
`

const NavMenu = styled.div`
    display: flex;

    @media(max-width: 768px) {
        display: none;
    }
`

const NavMenuLink = styled.a`
    color: #fff;
    text-decoration: none;
    font-weight: 700;
    margin-right: 32px;
    font-size: clamp(13px, .9vw, 18px);
    outline: none;
    :last-child {
        margin-right: 0;
    }
`

const MenuBars = styled.div`
    position: relative;
    display: none;
    flex-direction: column;
    align-items: center;
    @media(max-width: 992px) {
        display: flex;
    }

    span {
        display: block;
        height: 2px;
        width: 30px;
        background: #fff;
        transition: transform 1s cubic-bezier(.23,1,.32,1);
        
        &:not(:last-child) {
            margin-bottom: 8px;
        }
        &:first-child {
            transform: ${({ isOpen }) => isOpen ? 'rotate(-45deg) translateY(7px);' : ''};
        }
        &:last-child {
            transform: ${({ isOpen }) => isOpen ? 'rotate(45deg) translateY(-7px);' : ''};
        }
    }
`

const ControlElements = styled.div`
    display: flex;
    justify-content: space-between;

    @media(max-width: 992px) {
        display: none;
    }
`

const LanguageToggle = styled.div`
    margin-left: 32px;
    position: relative;
    min-width: 30px;
`

const LanguageCurrent = styled.div`
    color: #fff;
    font-size: clamp(13px, .9vw, 18px);
    font-weight: 700;
    outline: none;
    background: none;
    cursor: pointer;
    position: relative;
    z-index: 2;
    text-decoration: underline;
`

const LanguageItems = styled.div`
    position: absolute;
    top: -50%;
    left: 50%;
    transform: ${({ isLanguage }) => isLanguage.state ?  'translateY(0) translateX(-50%)' : 'translateY(-200%) translateX(-50%)'};
    background: #2F80ED;
    padding: 40px 15px 15px 15px;
    display: flex;
    flex-direction: column;
    transition: transform .2s ease-in-out;
`

const LanguageItem = styled.button`
    color: #fff;
    font-weight: 400;
    outline: none;
    background: none;
    cursor: pointer;
    border: none;
    margin-bottom: 5px;
    :last-child {
        margin-bottom: 0;  
    }
`

const Navbar = (props) => {
    const { t, i18n, ready } = useTranslation()

    const [isLanguage, setIsLanguage] = useState({
        state: false,
        currentLanguage: 'Укр',
        languages: ['Рус']
    });

    const menuLinks = t('menuLinks', { returnObjects: true })
    
    const changeLanguage = lang => {
        const setLang = lang === 'Рус' ? 'ru' : 'ua'
        const currentLang = lang === 'Рус' ? 'Рус' : 'Укр'
        const fallbackLang = currentLang === 'Рус' ? 'Укр' : 'Рус'

        setIsLanguage({...isLanguage, languages: [fallbackLang], currentLanguage: currentLang})

        i18n.changeLanguage(setLang)

        props.changeLanguage(!props.isChangeLanguage)
    }

    return (
        <Nav>
            <Container>
                <NavWrapper>
                    <img src={Logo} alt="swim system"/>
                    <ControlElements className="desktop-controls">
                        <NavMenu>
                        {
                            ready && menuLinks.map((item, index) => (
                                <NavMenuLink key={index} className="_text-on-load">
                                    {item}
                                </NavMenuLink>
                            ))
                        }
                        </NavMenu>
                        <LanguageToggle
                            onMouseEnter={() => setIsLanguage({...isLanguage, state: true})}
                            onMouseLeave={() => setIsLanguage({...isLanguage, state: false})}
                        >
                            <LanguageCurrent className="_text-on-load">{isLanguage.currentLanguage}</LanguageCurrent>
                            <LanguageItems isLanguage={isLanguage}>
                                {isLanguage.languages.map((lang, i) => <LanguageItem onClick={() => changeLanguage(lang)} key={i}>{lang}</LanguageItem>)}
                            </LanguageItems>
                        </LanguageToggle>
                    </ControlElements>  
                    <MenuBars onClick={() => props.menuHandler(!props.menuStatus)} isOpen={props.menuStatus}>
                        <span></span>
                        <span></span>
                    </MenuBars>          
                </NavWrapper>
            </Container>
        </Nav>
    )
}

export default Navbar