import React, { useEffect } from 'react'
import { TextLinesReveal } from '../../utils/textLinesReveal'
import gsap from 'gsap'

import { useTranslation } from 'react-i18next'

const TextAnimation = ({ loading, isChangeLanguage }) => {

    const { ready } = useTranslation()

    useEffect(() => {

        if(ready) {

          let DOM = {}

          gsap.set('._element-on-scroll', {
            yPercent: 320,
            rotate: 20
          })
          gsap.to('._element-on-scroll', {
            yPercent: 0,
            rotate: 0,
            scrollTrigger: {
              trigger: '.form-section',
              top: 'top top',
              scrub: true
            }
          })

          gsap.utils.toArray('._text-fill .back').forEach(text => {

              gsap.to(text, {
                  height: '0',
                  scrollTrigger: {
                    trigger: text,
                    start: '-=600',
                    end: '+=800',
                    scrub: true
                  }
              })
          })

          gsap.utils.toArray('._line-bar').forEach(line => {

            gsap.to(line, {
                width: '100%',
                scrollTrigger: {
                  trigger: line,
                  start: '-=600',
                  end: '+=800',
                  scrub: true
                }
            })
          })

          DOM.textContent = {
            textOnLoad: document.querySelectorAll('._text-on-load'),
            textOnScroll: document.querySelectorAll('._text-on-scroll')
          };

          const textLinesRevealOnLoad = new TextLinesReveal(
              [...DOM.textContent.textOnLoad]
          );
          const textLinesRevealOnScroll = new TextLinesReveal(
              [...DOM.textContent.textOnScroll]
          );

          const showContentOnLoad = () => {
            textLinesRevealOnLoad.in();
          };

          const hideContentOnLoad = () => {
            textLinesRevealOnLoad.out();
          };

          const showContentOnScroll = () => {
              textLinesRevealOnScroll.inScroll();
          };
          
          hideContentOnLoad()
          if(!loading) {
            showContentOnLoad()
          }
          
          if(!loading) {
            showContentOnScroll()
          }

        }
        
    }, [loading, ready])

    return (
        <div>
            
        </div>
    )
}

export default TextAnimation
